import React from 'react'
import { useEffect , useState}  from 'react'
import Home from './screens/Home'
import Notfound from './screens/Notfound'
import Login from './screens/Login'
import Register from './screens/Register'
import Booking from './screens/Booking'
import {BrowserRouter as Router, Routes, Route} from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import Bookingform from './screens/Bookingform'
import Ticket from './screens/Ticket'
import Bookingform1 from './screens/Bookingform1'
import Bookingform2 from './screens/Bookingform2'
import Bookingform3 from './screens/Bookingform3'
import Bookingform4 from './screens/Bookingform4'
import Bookingform5 from './screens/Bookingform5'
import Bookingform6 from './screens/Bookingform6'
import Bookingform7 from './screens/Bookingform7'
import Bookingform8 from './screens/Bookingform8'

export default function App() {

  const [backendData, setBackendData]  = useState([{}])
  
  useEffect(()=>{
    fetch("/api").then(
      Response => Response.json()
    ).then(
      data => {
        setBackendData(data)
      }
    )
  }, [])

let d = JSON.stringify(backendData);
  
return (
  <Router>
    <div>
      <Routes>
        <Route exact path="/Temple_Ticketing_System/" element={<Home/>} />
        <Route exact path="/" element={<Home/>} />
        <Route exact path='/Temple_Ticketing_System/login' element={<Login/>}/>
        <Route exact path='/Temple_Ticketing_System/booking' element={<Booking/>}/>
        <Route exact path='/Temple_Ticketing_System/register' element={<Register/>}/>
        <Route exact path='/Temple_Ticketing_System/bookingform' element={<Bookingform/>}/>
        <Route exact path='/Temple_Ticketing_System/ticket' element={<Ticket/>}/>
        <Route exact path='/Temple_Ticketing_System/bookingform1' element={<Bookingform1/>}/>
        <Route exact path='/Temple_Ticketing_System/bookingform2' element={<Bookingform2/>}/>
        <Route exact path='/Temple_Ticketing_System/bookingform3' element={<Bookingform3/>}/>
        <Route exact path='/Temple_Ticketing_System/bookingform4' element={<Bookingform4/>}/>
        <Route exact path='/Temple_Ticketing_System/bookingform5' element={<Bookingform5/>}/>
        <Route exact path='/Temple_Ticketing_System/bookingform6' element={<Bookingform6/>}/>
        <Route exact path='/Temple_Ticketing_System/bookingform7' element={<Bookingform7/>}/>
        <Route exact path='/Temple_Ticketing_System/bookingform8' element={<Bookingform8/>}/>
        <Route exact path='*' element={<Notfound/>}/>
      </Routes>
    </div>
    </Router>
  )
}
